@import 'mixins.scss';
@import 'variables.scss';

html, body  {
    font-family: 'Lato', sans-serif;
    height: 100%;
}
// #root{
//     height: 100%;
//     display: flex;
//     flex-direction: column;
// }
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}
li{
    list-style: none;
}
a {
    text-decoration: none;
}
button, input {
    border: none;
    background: none;
}
.container {
    display: flex;
    width: 1200px;
    justify-content: center;
    padding: 0px 15px;
    flex-direction: column;
    box-sizing: border-box;
    
}

.f-user-name {
    font-size: 14px;
    color: $fdark;
}

.f-user-spec {
    font-size: 13px;
    color: $fGray;
}

.f-text-bold {
    font-size: 13px;
    text-transform: capitalize;
    font-weight: bold;
    color: $fdark;
}
.f-text {
    font-size: 13px;
    color: $fdark;
}

.f-sub-text {
    font-size: 12px;
    text-transform: capitalize;
    color: $fGray;
}

.f-flex-column {
    @include columnCenter;
}
.f-card-wrapper {
    box-shadow: 0px 2px 7px $fShadow;
    margin-bottom: 20px;
}
.f-flex-row {
    @include rowCenter;
}

.f-row-start {
    align-items: flex-start !important;
}

.f-card-red {
    color: $flink;
}
.f-right-text-space {
    margin-right: 5px;
}

.f-white-backgroud {
    background-color: $fwhite;
}
.f-notif-content {
    width: 320px;
    border-top: 1px solid $fLightGray ;
    border-bottom: 1px solid $fLightGray;
    padding: 20px 0px;
    @include columnCenter;
    height: 200px;
}

.f-event-yellow-background {
    background-color: #FEF9EC;
}
.f-event-text-yellow-background {
    background-color: #f2c94c33;
}

.f-name-start-box {
    align-items: flex-start !important;
    margin-left: 10px;
}

.f-image {
    width: 100%;
    height: 100%;
}
.f-margin-right {
    margin-right: 10px;
}
.f-margin-top {
    margin-top: 5px;
}

.f-space-between {
    width: 100%;
    justify-content: space-between;
}
.f-hide-text {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    height: 1.8rem;
  
}

.f-link:hover {
    cursor: pointer;
}
.employee-content-wrapper-arrow:hover {
    cursor: pointer;
}
.f-box-shadow {
    box-shadow: 0px 1px 4px $fShadow;
}